@import url('https://fonts.googleapis.com/css2?family=Josefin+Sans:wght@400;700&display=swap');

*, *::before, *::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

/* Colors */

/* Primary */

:root {
  --bright-blue: hsl(220, 98%, 61%);
  --check-background: linear-gradient(hsl(192, 100%, 67%), hsl(280, 87%, 65%));
}

/* Neutral */

/* Light Theme */

:root {
  --very-light-gray: hsl(0, 0%, 98%);
  --very-light-grayish-blue: hsl(236, 33%, 92%);
  --light-grayish-blue: hsl(233, 11%, 84%);
  --dark-grayish-blue: hsl(236, 9%, 61%);
  --very-dark-grayish-blue: hsl(235, 19%, 35%);
}

/* Dark Theme */

:root {
  --very-dark-blue: hsl(235, 21%, 11%);
  --very-dark-desaturated-blue: hsl(235, 24%, 19%);
  --light-grayish-blue: hsl(234, 39%, 85%);
  --light-grayish-blue-hover: hsl(236, 33%, 92%);
  --dark-grayish-blue: hsl(234, 11%, 52%);
  --very-dark-grayish-blue-1: hsl(233, 14%, 35%);
  --very-dark-grayish-blue-2: hsl(237, 14%, 26%);
}

.hide {
  display: none !important;
}

img {
  max-width: 100%;
  cursor: pointer;
}

body {
  font-family: 'Josefin Sans', sans-serif;
  font-size: 18px;
  background-image: url(/src/images/bg-mobile-dark.jpg);
  background-repeat: no-repeat;
  background-size: 100% 285px;
  background-color: var(--very-dark-blue);
}

body.light {
  background-image: url(/src/images/bg-mobile-light.jpg);
}



section {
  margin: 5rem 2rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 2rem;
}

section .header {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
}

section .header h1 {
  color: white;
  font-size: 2.3rem;
  font-weight: 700;
  letter-spacing: 0.7rem;
}

section .input-container {
  display: flex;
  background-color: var(--very-dark-desaturated-blue);
  border-radius: 0.5rem;
  padding: 1.3rem;
  gap: 1rem;
}

section .input-container.light {
  background-color: var(--very-light-gray);
}





section .input-container input {
  background-color: transparent;
  border: none;
  outline: none;
  color: white;
  font-size: .9rem;
  font-weight: 700;
  width: 100%;
  font-family: inherit;
}

section .input-container input::placeholder {
  color: var(--light-grayish-blue);
  font-weight: 400;
  opacity: 0.5;
}

section .input-container input.light {
  color: black;
}

section .input-container input.light::placeholder {
  color: var(--dark-grayish-blue);
}

.empty-circle {
  width: 1.7rem;
  height: 1.5rem;
  border-radius: 50%;
  border: 1px solid var(--light-grayish-blue);
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}

section .todos .todo {
  cursor: move;
  display: flex;
  align-items: center;
  gap: 1rem;
  padding: 1.3rem;
  background-color: var(--very-dark-desaturated-blue);
  border-bottom: 1px solid var(--very-dark-grayish-blue);
}

section .todos .todo.light {
  background-color: var(--very-light-gray);
  border-bottom: 1px solid var(--light-grayish-blue);
}



section .todos .todo:first-child {
  border-top-left-radius: 0.5rem;
  border-top-right-radius: 0.5rem;
}

section .todos .todo .empty-circle {
  width: 1.9rem;
}

section .todos .todo p {
  color: white;
  font-size: .9rem;
  font-weight: 400;
  width: 100%;
  font-family: inherit;
}

section .todos .todo p.light {
  color: var(--dark-grayish-blue);
}

section .todos .todo.complete .empty-circle {
  background-image: url(/src/images/icon-check.svg);
  background-repeat: no-repeat;
  background-position: center;
  background-size: 60%;
  background-color: var(--bright-blue);
}

section .todos .todo.complete p {
  text-decoration: line-through;
  color: var(--dark-grayish-blue);
  opacity: 0.5;
}

section .todo-info {
  padding: 1.3rem;
  background-color: var(--very-dark-desaturated-blue);
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: var(--dark-grayish-blue);
  font-size: .9rem;
  margin-top: -2rem;
  border-bottom-left-radius: 0.5rem;
  border-bottom-right-radius: 0.5rem;
}

section .todo-info.light {
  background-color: var(--very-light-gray);
  color: var(--dark-grayish-blue);
}

section .todo-info button {
  background-color: transparent;
  border: none;
  outline: none;
  color: inherit;
  font-size: .8rem;
  font-weight: 700;
  font-family: inherit;
  cursor: pointer;
}

section .todos-state-buttons {
  padding: 1.3rem;
  background-color: var(--very-dark-desaturated-blue);
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 1.5rem;
}

section .todos-state-buttons.light {
  background-color: white;
}

body.light {
  background-color: var(--very-light-grayish-blue);
}

section .todos-state-buttons button {
  background-color: transparent;
  border: none;
  outline: none;
  color: var(--dark-grayish-blue);
  font-size: 1rem;
  font-weight: 700;
  font-family: inherit;
  cursor: pointer;
}

section .todos-state-buttons button.active {
  color: var(--bright-blue);
}

section .todos-state-buttons button:hover {
  color: var(--light-grayish-blue);
}

section p.message {
  color: var(--dark-grayish-blue);
  margin-top: 2rem;
  font-size: .9rem;
  font-weight: 700;
  text-align: center;
}

@media all and (min-width: 700px)
{
  body {
    background-image: url(/src/images/bg-desktop-dark.jpg);
    background-size: 100% 330px;
  }

  body.light {
    background-image: url(/src/images/bg-desktop-light.jpg);
  }

  main {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  section {
    min-width: 600px;
    }

  section .header h1 {
    font-size: 3.5rem;
    letter-spacing: 1rem;
  }

  section .input-container {
    padding: 2rem;
  }

  section .input-container input {
    font-size: 1.1rem;
  }

  section .input-container input::placeholder {
    font-size: 1.1rem;
  }

  section .todos .todo {
    padding: 2rem;
  }

  section .todos .todo p {
    font-size: 1.1rem;
  }

  section .todo-info {
    padding: 2rem;
  }

  section .todo-info p {
    font-size: 1.1rem;
  }

  section .todo-info button {
    font-size: 1rem;
  }

  section .todos-state-buttons {
    padding: 2rem;
  }

  section .todos-state-buttons button {
    font-size: 1.1rem;
  }


  section p.message {
    font-size: 1.1rem;
  }

  

}

@media all and (max-width: 400px)
{
  section .header h1 {
    font-size: 2rem;
    letter-spacing: 0.5rem;
  }

  section .input-container {
    padding: 1rem;
  }

  section .input-container input {
    font-size: 0.9rem;
  }

  section .input-container input::placeholder {
    font-size: 0.9rem;
  }

  section .todos .todo {
    padding: 1rem;
  }

  section .todos .todo .empty-circle {
    width: 1.8rem;
    height: 1.4rem;
  }

  section .todos .todo p {
    font-size: 0.7rem;
  }

  section .todos .todo img {
    width: .8rem;
  }

  section .todo-info {
    padding: 1rem;
  }

  section .todo-info p {
    font-size: 0.7rem;
  }
  
  section .todo-info button {
    font-size: 0.7rem;
  }


  section .todos-state-buttons {
    padding: 1rem;
  }

  section .todos-state-buttons button {
    font-size: 0.7rem;
  }

  section p.message {
    font-size: 0.7rem;
  }
}
